.cart {
	padding-bottom: 30px;
	&__title {
		margin: 0;
		background-color: #d0dfde;
		font-size: 30px;
		text-align: center;
		padding: 30px;
	}
	&__empty {
		font-size: 30px;
		text-align: center;
		margin-top: 30px;
	}
	> .button {
		margin-top: 15px;
		font-size: 20px;
		svg {
			margin-left: 5px;
		}
	}
}
.choice-lists {
	margin: 0 -15px;
	&::after {
		content: "";
		clear: both;
		display: block;
	}
	> div {
		width: calc(50% - 30px);
		float: left;
		margin: 0 15px;
	}
}
